.search-container {
  display: flex;
  align-items: center;
  background-color: var(--color-white);
  width: 30%;
  padding: 1rem 2rem;
  border-radius: 1rem;
}

.main-container.dark .search-container {
  background-color: var(--dark-blue-elements);
}

.search-icon {
  margin-right: 4px;
}

.main-container.dark .search-icon {
  color: var(--color-white);
}

.search-container form {
  flex: 1;
}

.search-input {
  font-family: "Nunito Sans", sans-serif;

  border: none;
  width: 100%;

  padding: 1rem;
}

.main-container.dark .search-input {
  background-color: var(--dark-blue-elements);
  color: var(--color-white);
}

.main-container.dark .search-input::placeholder {
  color: var(--color-white);
}

.search-input:focus {
  outline: none;
}
